.b-about-presentation__header {
  text-align: center; }

@media (max-width: 767px) {
  .b-about-presentation__photo {
    margin-bottom: 20px; } }

@media (min-width: 768px) and (max-width: 1199px) {
  .b-about-presentation__photo {
    margin-bottom: 38px; } }

@media (min-width: 1200px) {
  .b-about-presentation__photo {
    margin-bottom: 27px; } }

@media (max-width: 1199px) {
  .b-about-presentation__title {
    margin-bottom: 14px; } }

@media (min-width: 1200px) {
  .b-about-presentation__title {
    margin-bottom: 25px; } }

@media (max-width: 767px) {
  .b-about-presentation__content {
    padding-left: 10px;
    padding-right: 10px; } }



